<template>
    <p-data-table
        :value="drugChartForm"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        :scrollable="true"
        scrollDirection="horizontal"
        editMode="cell"
    >
        <template #header>
            <div class="table-header">
                Drugs
            </div>
        </template>

        <p-column
            header="Name"
            field="name"
            style="min-width: 135px; min-height: 30px;"
            frozen
        >
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.name"
                    style="min-width: 120px; max-width: 120px;"
                />
            </template>
        </p-column>
        <p-column
            header="Dose"
            field="dose1"
            class="dose"
            style="min-width: 135px"
        >
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.dose1"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Time" field="time1" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.time1"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Initial" field="initial1" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.initial1"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>

        <p-column
            header="Dose"
            field="dose2"
            class="dose"
            style="min-width: 135px"
        >
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.dose2"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Time" field="time2" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.time2"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Initial" field="initial2" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.initial2"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>

        <p-column
            header="Dose"
            field="dose3"
            class="dose"
            style="min-width: 135px"
        >
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.dose3"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Time" field="time3" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text v-model="data.time3" />
            </template>
        </p-column>
        <p-column header="Initial" field="initial3" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.initial3"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>

        <p-column
            header="Dose"
            field="dose4"
            class="dose"
            style="min-width: 135px"
        >
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.dose4"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Time" field="time4" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.time4"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Initial" field="initial4" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.initial4"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>

        <p-column
            header="Dose"
            field="dose5"
            class="dose"
            style="min-width: 135px"
        >
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.dose5"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Time" field="time5" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.time5"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Initial" field="initial5" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.initial5"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>

        <p-column
            header="Dose"
            field="dose6"
            class="dose"
            style="min-width: 135px"
        >
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.dose6"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Time" field="time6" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.time6"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Initial" field="initial6" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.initial6"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>

        <p-column
            header="Dose"
            field="dose7"
            class="dose"
            style="min-width: 135px"
        >
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.dose7"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Time" field="time7" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.time7"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
        <p-column header="Initial" field="initial7" style="min-width: 80px">
            <template #editor="{ data }">
                <p-input-text
                    v-model="data.initial7"
                    @input="formatToLine(data)"
                />
            </template>
        </p-column>
    </p-data-table>
</template>

<script>
import { inject, onMounted, onUnmounted } from "vue";
import { useDrugChartForm, useFormatToDrugLine } from "@/composable/opu";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();
        const drugChartForm = useDrugChartForm();

        const onSummaryLoaded = () => {
            drugChartForm.value = store.getters["opu/getDrugChart"];
        };

        const opuForm = inject("opuForm");
        const formatToLine = (data) => {
            if (data.key === "ativanLine") {
                opuForm.ativanLine = useFormatToDrugLine(data);
            } else if (data.key === "versedLine") {
                opuForm.versedLine = useFormatToDrugLine(data);
            } else if (data.key === "fentanylLine") {
                opuForm.fentanylLine = useFormatToDrugLine(data);
            } else if (data.key === "narcanLine") {
                opuForm.narcanLine = useFormatToDrugLine(data);
            } else if (data.key === "atropineLine") {
                opuForm.atropineLine = useFormatToDrugLine(data);
            } else if (data.key === "gravolLine") {
                opuForm.gravolLine = useFormatToDrugLine(data);
            } else if (data.key === "xylocaineLine") {
                opuForm.xylocaineLine = useFormatToDrugLine(data);
            } else if (data.key === "other1Line") {
                opuForm.other1Name = data.name;
                opuForm.other1Line = useFormatToDrugLine(data);
            } else if (data.key === "other2Line") {
                opuForm.other2Name = data.name;
                opuForm.other2Line = useFormatToDrugLine(data);
            } else if (data.key === "other3Line") {
                opuForm.other3Name = data.name;
                opuForm.other3Line = useFormatToDrugLine(data);
            } else if (data.key === "other4Line") {
                opuForm.other4Name = data.name;
                opuForm.other4Line = useFormatToDrugLine(data);
            } else if (data.key === "other5Line") {
                opuForm.other5Name = data.name;
                opuForm.other5Line = useFormatToDrugLine(data);
            } else if (data.key === "other6Line") {
                opuForm.other6Name = data.name;
                opuForm.other6Line = useFormatToDrugLine(data);
            } else if (data.key === "other7Line") {
                opuForm.other7Name = data.name;
                opuForm.other7Line = useFormatToDrugLine(data);
            } else if (data.key === "other8Line") {
                opuForm.other8Name = data.name;
                opuForm.other8Line = useFormatToDrugLine(data);
            } else if (data.key === "other9Line") {
                opuForm.other9Name = data.name;
                opuForm.other9Line = useFormatToDrugLine(data);
            }
        };

        onMounted(() => {
            emitter.on("opu:summary-loaded", onSummaryLoaded);
        });

        onUnmounted(() => {
            emitter.off("opu:summary-loaded", onSummaryLoaded);
        });

        return { drugChartForm, formatToLine };
    }
};
</script>

<style scoped>
.p-inputtext {
    width: 65px;
    max-width: 65px;
    min-width: 65px;
}

.dose .p-inputtext {
    min-width: 120px;
}
</style>
