<script>
import {
    useBornMarkAsComplete,
    useOocyteCollectionInit
} from "@/composable/born";
import {
    computed,
    inject,
    onMounted,
    onUnmounted,
    toRefs,
    watch
} from "vue-demi";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";
import { emitter } from "@/event-bus";
import { TIME_0 } from "@/constants";
import { getFertilizatinStartDate } from "@/service/opu";

export default {
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            store,
            form,
            loading,
            v$,
            save,
            fillForm,
            oocyteOriginOptions,
            donorProviderStatusOptions,
            oocyteCryoMethodOptions,
            spermOriginOptions,
            spermOriginStatusOptions,
            spermSourceOptions
        } = useOocyteCollectionInit();

        const { isInBornPage } = toRefs(props);

        watch(() => store.state.born.oocyteCollections, fillForm);

        watch(
            () => form.retrievedOrThawed,
            (newVal) => {
                store.commit("born/SET_OOCYTES_RETRIEVED", newVal);
            }
        );

        watch(
            () => form.spermTooLowCount,
            (newVal) => {
                if (newVal == "Y") {
                    form.spermCount = null;
                }
            }
        );

        watch(
            () => form.oocyteCryoPreserved,
            (newVal) => {
                store.commit("born/SET_OOCYTE_CRYO_PRESERVED", newVal);
            }
        );

        const bornForm = inject("bornForm", null);

        watch(form, (newValue) => {
            if (bornForm == null) return;
            bornForm.oocyteCollection = {
                ...newValue,
                donorProviderSource: form.oocytesProviderSource,
                oocyteCollectionDate: form.oocyteCollectionDate
                    ? format(
                          new Date(form.oocyteCollectionDate + TIME_0),
                          DATE_FORMAT_STRING
                      )
                    : null,
                oocyteThawDate: form.oocyteThawDate
                    ? format(
                          new Date(form.oocyteThawDate + TIME_0),
                          DATE_FORMAT_STRING
                      )
                    : null,
                oocyteFreezeDate: form.oocyteFreezeDate
                    ? format(
                          new Date(form.oocyteFreezeDate + TIME_0),
                          DATE_FORMAT_STRING
                      )
                    : null
            };
        });

        const isEggFreezing = computed(
            () => store.state.artCycle.selectedCycle.cycleType == "Egg Freezing"
        );

        onMounted(() => {
            emitter.on("born:save-temp", save);

            getFertilizatinStartDate(
                store.state.artCycle.selectedCycle.cycleId
            ).then((resp) => {
                const { startDate } = resp;
                if (startDate && form.oocyteCollectionDate == null) {
                    form.oocyteCollectionDate = new Date(startDate + TIME_0);
                }
            });

            store
                .dispatch(
                    "fertilization/getFertilization",
                    store.state.artCycle.selectedCycle.cycleId
                )
                .then(() => {})
                .catch(() => {});

            if (isInBornPage.value) {
                return;
            }

            store.dispatch(
                "born/getBronV2",
                store.state.artCycle.selectedCycle
            );

            store.dispatch(
                "fertilization/getFertilization",
                store.state.artCycle.selectedCycle.cycleId
            );
        });

        onUnmounted(() => {
            emitter.off("born:save-temp", save);
        });

        const { markAsComplete } = useBornMarkAsComplete();

        return {
            isInBornPage,
            form,
            loading,
            save,
            v$,
            fillForm,
            isEggFreezing,
            oocyteOriginOptions,
            donorProviderStatusOptions,
            oocyteCryoMethodOptions,
            spermOriginOptions,
            spermOriginStatusOptions,
            spermSourceOptions,
            markAsComplete
        };
    }
};
</script>

<style scoped>
.p-calendar,
