<template>
    <iframe :src="formSrc" frameborder="0" :on-load="onLoadHandler" />
</template>

<script>
import { inject } from "vue-demi";

export default {
    setup() {
        const formSrc = inject("formSrc");

        const onLoadHandler = () => {
            console.log("load");
        };

        return {
            formSrc,
            onLoadHandler
        };
    }
};
</script>

<style scoped>
iframe {
    background-color: white;
    width: 100%;
    height: calc(100vh - 160px);
}
</style>
